	module.exports = {"data":{"wordpressPost":{"title":"BMJ Open&#8217;s first birthday","content":"<p> </p>\n<p>It is a year today that BMJ Open published its first papers: prompting <a href=\"http://yfrog.com/h7ma7kmj\" target=\"_blank\">donuts all round</a>!</p>\n<p>We have now published over 230 open access research articles, covering niche topics and major public health issues alike.</p>\n<p>Several articles have received plenty of press coverage. Some have prompted considerable national debate, such as <a href=\"bmjopen.bmj.com/content/1/1/e000263.abstract?\" target=\"_blank\">this paper </a>suggesting that there would be major benefits to the rest of the UK if their diets improved to the level of England&#8217;s. Others, such as <a href=\"http://bmjopen.bmj.com/content/1/1/e000039.abstract\" target=\"_blank\">this paper </a>on locked-in syndrome, have received widespread international coverage. Many articles now have thousands of downloads.</p>\n<p>Wider exposure can bring closer scrutiny and some articles have received plenty of robust critique after publication. Everything we publish has received peer review, quite often including statistical review. We publish reviews of accepted articles so the justification for acceptance is clear. But research thrives on debate and we would be delighted to receive more comments &#8211; positive or (constructively) critical.</p>\n<p>After just a year of publication we feel the journal is already making its mark. We have published papers from first-time authors and experienced researchers, offering a straightforward route to publication via fair, transparent peer review.</p>\n<p>Many authors have also benefited from our willingness to waive article payment charges for those without the means to pay.</p>\n<p>In our next year we look forward to publishing an even wider range of papers, hopefully with more authors taking advantage of our partnership with the Dryad repository to share their research data.</p>\n<p>Here&#8217;s to the next 12 months &#8230;<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"1468bb61-8480-5006-931b-091b9d5035c0"}}

